.infoBox-root {
  width: 100%;
  max-width: 500px;
  height: 80vh;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.infoBox-bg {
  max-width: 500px;
  height: 100% !important;
  position: absolute;
  object-fit: contain;
  top: 50%;
  left: 50%;
  transform: translate(-49%, -50%);
}
.infoBox-bg-inner {
  height: 82%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-48%, -50%);
}
.infoBox-container {
  width: 65%;
  height: 100%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.infoBox-content {
  width: 100%;
  height: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
}
.infoBox-imgContainer {
  margin: 0 auto;
}
.infoBox-titleImage {
  width: 100%;
  height: 100%;
  max-height: 150px;

  border-radius: 15px;
  border: solid 10px rgba(128, 128, 128, 0.289);
}
.infoBox-title {
  text-align: center;
  font-size: 25pt;
  font-weight: 900;
  color: rgb(67, 67, 67);
}
.infoBox-sm {
  font-weight: bolder;
}
.infoBox-hr {
  height: 2px;
  width: 100%;
  background-color: rgba(128, 128, 128, 0.583);

  margin-bottom: 15px;
}
.infoBox-text {
  display: flex;
  flex-grow: 1;
  font-weight: bolder;
}
/*
.infoBox-btn {
  position: absolute;
  bottom: 10%;
  right: 0;
}*/
.infoBox-optsContainer {
  max-height: 150px;
  padding-bottom: 50px;
  display: flex;
  justify-content: flex-end;
}
.infoBox-btn img {
  cursor: pointer;
  height: 100%;
  object-fit: contain;
}

@media screen and (min-width: 1100px) {
  .infoBox-root {
    left: unset;
    right: 0;
  }
}
